import React, {Component, FormEvent} from "react";
import "./styles.scss";
import * as worms from "./worms";
import {RemoteSearch, SearchInstance} from "./RemoteSearch";
import {Button, Checkbox, Segment, Table} from "semantic-ui-react";
import {EvaluationControls, InitialStateModal, MultipleEvaluations, RChest, REvaluation, RState} from "./components";
import {CheckboxProps} from "semantic-ui-react/dist/commonjs/modules/Checkbox/Checkbox";

const remoteSearch = RemoteSearch.default();

// eslint-disable-next-line @typescript-eslint/no-empty-interface
interface AppProps {
}
interface AppState {
  state: worms.State,
  progress: number,
  evaluation: worms.Evaluation,
  searching: boolean,
  searchFinished: boolean,
  dicePickEvaluations: {pickedRoll: worms.RollResult, pickedCount: number, evaluation: worms.Evaluation}[] | null,
  visibleRollPicks: worms.RollResult[],
  cacheStats: worms.EvaluationCacheStats,
}

export default class App extends Component<AppProps, AppState> {
  state = {
    state: worms.UnrolledState.initial(),
    progress: 1,
    evaluation: worms.Evaluation.empty(),
    searching: false,
    searchFinished: true,
    dicePickEvaluations: null,
    visibleRollPicks: worms.rollResults,
    cacheStats: {hitCount: 0, missCount: 0, entryCount: 0},
  };

  onSearchResult = (
    searching: boolean, searchFinished: boolean, progress: number, evaluation: worms.Evaluation,
    dicePickEvaluations: {pickedRoll: worms.RollResult, pickedCount: number, evaluation: worms.Evaluation}[] | null,
    cacheStats: worms.EvaluationCacheStats,
  ) => {
    this.setState({
      progress,
      evaluation: evaluation.toFixed(),
      searchFinished,
      searching,
      dicePickEvaluations,
      visibleRollPicks: worms.rollResults,
      cacheStats,
    });
  };

  searchInstance: SearchInstance = remoteSearch.newInstance(this.onSearchResult);

  componentDidMount() {
    this.onReset();
  }

  componentWillUnmount() {
    this.searchInstance.removeSearch();
  }

  render() {
    const {
      state, progress, evaluation, searching, searchFinished, dicePickEvaluations, visibleRollPicks, cacheStats,
    } = this.state;
    return (
      <div className="App">
        <h2>Search</h2>
        <label>
          Initial state:
          <RState size={"tiny"} state={state} />
        </label>
        <InitialStateModal size={"tiny"} trigger={<Button>Change</Button>} onStateChange={this.onStateChange} />
        <EvaluationControls
          progress={progress}
          searching={searching}
          searchFinished={searchFinished}
          onSearchStep={this.onSearchStep}
          onSearchToggle={this.onSearchToggle}
          onSearchRestart={this.onSearchRestart}
          cacheStats={cacheStats}
          searchInstance={this.searchInstance}
        />
        <REvaluation evaluation={evaluation} />
        {dicePickEvaluations ? <>
          <Segment>
            <Table definition collapsing unstackable size={"small"}>
              <Table.Header>
                <Table.HeaderCell />
                <Table.HeaderCell>Pick</Table.HeaderCell>
                <Table.HeaderCell>Expected Value</Table.HeaderCell>
                <Table.HeaderCell>Visible</Table.HeaderCell>
              </Table.Header>
              <Table.Body>
                {(dicePickEvaluations as {pickedRoll: worms.RollResult, pickedCount: number, evaluation: worms.Evaluation}[]).map(({evaluation, pickedRoll, pickedCount}) => (
                  <Table.Row key={pickedRoll}>
                    <Table.Cell>Pick {pickedRoll}</Table.Cell>
                    <Table.Cell><RChest chest={worms.Chest.fromDiceRoll(new worms.DiceRoll([[pickedRoll, pickedCount]]))} remainingDice={0} size={"tiny"} /></Table.Cell>
                    <Table.Cell>{evaluation.expectedValue.toFixed(1)}</Table.Cell>
                    <Table.Cell><Checkbox toggle checked={visibleRollPicks.includes(pickedRoll)} onChange={this.makeOnRollVisibleChange(pickedRoll)} /></Table.Cell>
                  </Table.Row>
                ))}
              </Table.Body>
            </Table>
          </Segment>
          <MultipleEvaluations evaluationsAndPickedRolls={dicePickEvaluations} visibleRollPicks={visibleRollPicks} />
        </> : null}
      </div>
    );
  }

  onStateChange = (state: worms.State) => {
    this.setState({state});
    this.searchInstance.setSearchState(state);
  };

  onReset = () => {
    const {state} = this.state;
    this.searchInstance.setSearchState(state);
  };

  onSearchStep = () => {
    this.searchInstance.stepSearch();
  };

  onSearchToggle = () => {
    if (this.state.searching) {
      this.stopSearch();
    } else if (!this.state.searchFinished) {
      this.startSearch();
    }
  };

  onSearchRestart = () => {
    this.setState({searching: false, searchFinished: false});
    this.searchInstance.setSearchState(this.state.state);
  };

  startSearch() {
    this.searchInstance.startSearch();
  }

  stopSearch() {
    this.searchInstance.stopSearch();
  }

  makeOnRollVisibleChange(pickedRoll: worms.RollResult): (ev: FormEvent<HTMLInputElement>, data: CheckboxProps) => void {
    return (_ev: FormEvent<HTMLInputElement>, {checked}) => {
      this.setState(({visibleRollPicks}) => {
        if (checked && !visibleRollPicks.includes(pickedRoll)) {
          return {visibleRollPicks: [...visibleRollPicks, pickedRoll]};
        } else if (!checked && visibleRollPicks.includes) {
          return {visibleRollPicks: visibleRollPicks.filter(otherRoll => otherRoll !== pickedRoll)};
        } else {
          return null;
        }
      });
    };
  }
}
